.todo-item {
  display: flex;
  align-items: center;
  background: white;
  border-radius: 5px;
  box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.1);
  background: -webkit-gradient(
      linear,
      0 0,
      0 100%,
      from(#d9eaf3),
      color-stop(4%, #fff)
    )
    0 4px;
  background-size: 100% 3rem;
  line-height: 3rem;
  margin: 0.5rem 0;
  padding: 10px 65px 10px 10px;
  min-width: 600px;
  max-width: 70%;
  min-height: 45px;
  height: auto;
  position: relative;
}

.todo-item:before {
  content: "";
  position: absolute;
  width: 4px;
  top: 0;
  right: 80px;
  bottom: 0;
  border: 1px solid;
  border-color: transparent #e0aa7e;
}

.todo-item__checkbox {
  display: none;
}

.checkbox-btn {
  width: 24px;
  height: 24px;
  border: none;
  border-radius: 50%;
  margin-right: 1rem;
  background-color: #dbe79f;
}

.checkbox-btn > svg {
  display: none;
  width: 100%;
  height: 100%;
}

.checkbox--filled {
  border: none;
  width: 26px;
  height: 26px;
}

.checkbox--filled > svg {
  display: block;
}

.todo-item__value {
  display: inline-flex;
  align-items: center;
  width: 95%;
  text-align: justify;
  margin: 0;
  color: #2b1000;
  font-size: 1.3rem;
}

.task--done {
  font-style: italic;
  text-decoration: line-through;
  font-weight: 400;
  color: #32ba7c;
}

.todo-item:last-child {
  margin-bottom: 0;
}

.todo-item__buttons-container {
  position: absolute;
  right: 5px;
  display: flex;
  justify-content: space-around;
  width: 70px;
}

.todo-item__input {
  display: block;
  box-sizing: border-box;
  -webkit-task-sizing: border-box;
  -moz-box-sizing: border-box;
  border: 2px solid #dfbe5a;
  width: 92%;
  padding: 10px;
  border-radius: 5px;
  font-size: 1.3rem;
}
