.btn {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin: 2px 0 7px 0;
  background-color: transparent;
  border: none;
}

.btn--yellow:hover {
  background-color: #ffca2a;
}

.btn--blue:hover {
  background-color: #32a1be;
  box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.1);
}

.btn--pink:hover {
  background-color: #ee8f75;
}

.btn--green:hover {
  background-color: #7ad750;
}

.btn-aligner {
  display: flex;
  justify-content: center;
  align-items: center;
}

.todo-form {
  display: flex;
  width: 600px;
  position: relative;
}

.todo-form_input {
  width: 85%;
  height: 2rem;
  border: none;
  border-bottom: 2px solid #f9a400;
  padding-top: 0.2rem;
  padding-left: 0.6rem;
  margin-right: 20px;
  border-radius: 5px;
  box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.1);
}

.input--error {
  background-color: tomato;
}

.error-message {
  display: none;
  position: absolute;
  z-index: 2;
  background-color: #ead7d7;
  padding: 5px;
  border: 1px solid tomato;
  bottom: -33px;
  margin-left: 10px;
  font-size: 14px;
}

.error--active {
  display: block;
}

.new-task__btn {
  border: none;
  background-color: #1e87a3;
  border-radius: 50%;
  font-size: 1.8rem;
  font-weight: bold;
  width: 2rem;
  height: 2rem;
  padding: 0;
}

.header-container {
  width: inherit;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.task-filter-btn {
  padding: 5px 5px 0 5px;
  border: none;
  border-radius: 5px;
  height: 30px;
  color: #fff;
  font-weight: bold;
  margin-right: 20px;
  text-decoration: none;
}

.btn--all {
  background-color: #8879ab;
}

.btn--all:hover {
  background-color: #8879ab;
  box-shadow: 5px 5px 0 #ccc;
}

.btn--todo {
  background-color: #ba6732;
}

.btn--todo:hover {
  background-color: #ba6732;
  box-shadow: 5px 5px 0 #ddac8d;
}

.btn--done {
  background-color: #32ba7c;
}

.btn--done:hover {
  background-color: #32ba7c;
  box-shadow: 5px 5px 0 #a5e3c7;
}

.link--selected {
  height: 26px;
  border: 2px solid #fff;
}

.loader {
    color: #6383a8;
    font-size: 20px;
    margin: 100px auto;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    position: relative;
    text-indent: -9999em;
    -webkit-animation: load4 1.3s infinite linear;
    animation: load4 1.3s infinite linear;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
  @-webkit-keyframes load4 {
    0%,
    100% {
      box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
    }
    12.5% {
      box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    25% {
      box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    37.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    50% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    62.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
    }
    75% {
      box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
    }
    87.5% {
      box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
    }
  }
  @keyframes load4 {
    0%,
    100% {
      box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
    }
    12.5% {
      box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    25% {
      box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    37.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    50% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    62.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
    }
    75% {
      box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
    }
    87.5% {
      box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
    }
  }
  
.todo-item {
  display: flex;
  align-items: center;
  background: white;
  border-radius: 5px;
  box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.1);
  background: -webkit-gradient(
      linear,
      0 0,
      0 100%,
      from(#d9eaf3),
      color-stop(4%, #fff)
    )
    0 4px;
  background-size: 100% 3rem;
  line-height: 3rem;
  margin: 0.5rem 0;
  padding: 10px 65px 10px 10px;
  min-width: 600px;
  max-width: 70%;
  min-height: 45px;
  height: auto;
  position: relative;
}

.todo-item:before {
  content: "";
  position: absolute;
  width: 4px;
  top: 0;
  right: 80px;
  bottom: 0;
  border: 1px solid;
  border-color: transparent #e0aa7e;
}

.todo-item__checkbox {
  display: none;
}

.checkbox-btn {
  width: 24px;
  height: 24px;
  border: none;
  border-radius: 50%;
  margin-right: 1rem;
  background-color: #dbe79f;
}

.checkbox-btn > svg {
  display: none;
  width: 100%;
  height: 100%;
}

.checkbox--filled {
  border: none;
  width: 26px;
  height: 26px;
}

.checkbox--filled > svg {
  display: block;
}

.todo-item__value {
  display: inline-flex;
  align-items: center;
  width: 95%;
  text-align: justify;
  margin: 0;
  color: #2b1000;
  font-size: 1.3rem;
}

.task--done {
  font-style: italic;
  text-decoration: line-through;
  font-weight: 400;
  color: #32ba7c;
}

.todo-item:last-child {
  margin-bottom: 0;
}

.todo-item__buttons-container {
  position: absolute;
  right: 5px;
  display: flex;
  justify-content: space-around;
  width: 70px;
}

.todo-item__input {
  display: block;
  box-sizing: border-box;
  -webkit-task-sizing: border-box;
  -moz-box-sizing: border-box;
  border: 2px solid #dfbe5a;
  width: 92%;
  padding: 10px;
  border-radius: 5px;
  font-size: 1.3rem;
}

.tasks-list {
  width: 100%;
  height: auto;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
}

.page-container {
  width: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  max-width: 100%;
  min-height: 100vh;
  background-color: #dbe79f;
  overflow-y: scroll;
}

